html, body {
    /* background-color: rgb(174, 174, 174); */
    height: 100%;
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
 /* overflow: hidden;  */
}
.wrapper{
    margin:auto;
    width:100%;
    height:100%;
}
#webgl{
    position: absolute;
    top: 0;
    width:100%;
    height:100%; 
}
.footerContainer{
    z-index: 1;
}
.countryContainer{
    z-index: 1;
}
.tapToPlace{
    position: absolute;
    top: 0;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 100%;
}
.tabContent{
    color: white;
    font-size: 26px;
    font-weight: bold;
    background: #00000069;
    padding: 20px;
    border-radius: 10px;
    position: absolute;
    bottom: 181px;

}
@media (min-width:320px) {
.ui-wrapper{
    position: absolute;
    /* background-color: red; */
    height: 100%;
    width: 100%;
}
.headerContainer{
    background-color:black;
    height: 10%;
    width: 100%;
    position: relative;
    z-index: 100;
}
.headerContainer img{
    margin-top:2%;
    width: 130px;
    margin-left:4% ;
}
/* -------------------------------------------------------------------COUNTRY */
.countryContainer{
    /* background-color: green; */
    /* height: 7%; */
    width:30%;
    float: left;
    margin-top:8%;
    margin-left:5%;
    
}
 .inningsOneCountryWrapper {
    /* background-color: blue; */
    float: left;
    width: 50%;
    text-align: center;
}
.inningsTwoCountryWrapper{
    /* background-color: yellow; */
    float: right;
    width: 50%;
    text-align: center;
}
.inningsOneCountryWrapper p , .inningsTwoCountryWrapper p{
    position: relative;
    background-color: black;
    font-size: 10px;
    color:white;
    border-radius: 8px; 
    border: 1px solid white;
    padding-left:2px;
    padding-right:2px;
     padding-top: 8px; 
    padding-bottom: 8px;
    width: 80%;
    margin:auto;
    z-index: 1;
}
/* ----------------------------------------------------------RIGHT SCORE */
.teamScoreContainer{
    float: right;
    width: 107px;
    margin-top: 26px;
    margin-right: 1px;
    position: relative;
    z-index: 100;
}

.teamFlagContainer{
    /* background-color: blue; */
    float:left
}
.teamFlagContainer img{
    width:40px;
    border-radius: 20px;
    margin-top:-7px;
}

.scoreWrapper{
    /* background-color: blueviolet; */
    float: right;
    margin-right:10px;
}
.scoreWrapper h1{
    margin: 0;
    font-size: 15px;
    color:white;
}
.scoreWrapper p{
    margin: 0;
    font-size:10px;
    color:white;
    /* background-color: red; */
    /* width:50%; */
}
/*------------------------------------------------------------ PLAYER SCROLLER */
.playerScrollerContainer{
    /* background-color: brown; */
    margin-top:50%;
    width: 50px;
    height:15%;
    margin-left:5%;           
}
.playerScrollerContainer img{
   
    display: block;
   margin-left:auto;
   margin-right:auto;
}
.inningsContainer{
    width:100%;
    height:100%;
}
.firstInningsPlayer{
    /* background-color: grey; */
    width:100%;
    height:100%;
    overflow-y: hidden;
}
.secondInningsPlayer{
    /* background-color: green; */
    width:100%;
    height:100%;
    overflow-y: hidden;
    display: none;
}

/*------------------------------------------------------------------ FOOTER */
.footerContainer{
    /* background-color: grey; */
    width: 100%;
    position: absolute;
    bottom: 20px;
    text-align:center;
}
.scoreList{
    width: 85%;
    padding: 0;
    display: flex;
    justify-content: space-between;
    margin: auto;
}
.footerContainer ul{
    /* background-color:yellow;  */
    /* margin:auto;
    width:100%; */
    
    /* float:left */
}
.footerContainer ul li{
    list-style: none;
    background-color: black;
    padding: 7px 15px 6px 15px;
    font-size: 14px;
    border-radius: 5px;
    font-family: Arial, sans-serif;
}
.swiper-container{
    height: 177px;
    top: 237px;
    left: -13px; 
}
.swiper-slide img{
    top: 0px;
    left: 43px;
    width: 45px;
    height: 45px;
}
.up-icon{
    top: 216px;
    left: 42px;
}
.down-icon {
    position: absolute;
    top: 419px;
    left: 44px;
}
.swiper-section {
    height: 244px;
}
}
